body {
    margin: 0;
    background-image: url('../public/background.gif');
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.app {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.yesButton {
    border-radius: 5px;
    background-color: pink;
    color: red;
    border-color: pink;
    width: 100px;
    height: 40px;
    position: absolute;
    left: 55%;
    bottom: 20%
}

